<template>
  <v-ons-splitter>
    <v-ons-splitter-side swipeable width="150px" collapse="" side="left" :open.sync="openSide">
      <v-ons-page>
        <v-ons-list>
          <v-ons-list-item v-for="(page, index) in pages" :key="index" tappable modifier="chevron" @click="currentPage = page; openSide = false">
            <div class="center">{{ page }}</div>
          </v-ons-list-item>
        </v-ons-list>
      </v-ons-page>
    </v-ons-splitter-side>

    <v-ons-splitter-content>
      <component :is="currentPage" :toggle-menu="() => openSide = !openSide" :articles_business="articles_business" :articles_entertainment="articles_entertainment" :articles_health="articles_health" :articles_science="articles_science" :articles_sports="articles_sports" :articles_technology="articles_technology" :loading="loading"></component>
    </v-ons-splitter-content>
  </v-ons-splitter>
</template>

<script>
import db from '@/firebase/init'

import Business from './Business'
import Entertainment from './Entertainment'
import Health from './Health'
import Science from './Science'
import Sports from './Sports'
import Technology from './Technology'
import About from './About'
export default {
  name: 'Main',
  components:{
    Business,
    Entertainment,
    Health,
    Science,
    Sports,
    Technology,
    About
  },
  data () {
    return {
      currentPage: '',
      pages : ['Business','Entertainment','Health','Science','Sports','Technology','About'],
      openSide : false,
      articles:[],
      loading:true
    }
  },
  methods:{
    todayIs(){
      var today = new Date();
      var dd = String(today.getDate()).padStart(2, '0')
      var mm = String(today.getMonth() + 1).padStart(2, '0') //January is 0!
      var yyyy = today.getFullYear()

      today = yyyy + '-' + mm + '-' + dd
      return today
    },
    fetchData(){
      var todayNewsRef = db.ref('news/'+this.todayIs())
      if(sessionStorage.getItem('snapshotVal')){
        this.articles = Object.values(JSON.parse(sessionStorage.snapshotVal))
        this.loading = false
        var key = sessionStorage.lastClickedNews
        setTimeout(function(){ location.href = "#"+key }, 2000)
      } else {
        todayNewsRef.once('value', function(snapshot) {
          // turn response object into array
          sessionStorage.setItem('snapshotVal',JSON.stringify(snapshot.val()))
          this.articles = Object.values(snapshot.val())
          this.loading = false
        }.bind(this))
      }
    },
    showPage(){
      var lastViewdPage = sessionStorage.getItem("lastViewdPage")
      if(!lastViewdPage){
        lastViewdPage = 'Business'
      }
      this.currentPage = lastViewdPage
    }
  },
  computed: {
    articles_business: function () {
      var result = []
      for(var i=0; i < this.articles.length; i++){
        if(this.articles[i].category == "business"){
          result.push(this.articles[i])  
        }
      }
      return result
    },
    articles_entertainment: function () {
      var result = []
      for(var i=0; i < this.articles.length; i++){
        if(this.articles[i].category == "entertainment"){
          result.push(this.articles[i])  
        }
      }
      return result
    },
    articles_health: function () {
      var result = []
      for(var i=0; i < this.articles.length; i++){
        if(this.articles[i].category == "health"){
          result.push(this.articles[i])  
        }
      }
      return result
    },
    articles_science: function () {
      var result = []
      for(var i=0; i < this.articles.length; i++){
        if(this.articles[i].category == "science"){
          result.push(this.articles[i])  
        }
      }
      return result
    },
    articles_sports: function () {
      var result = []
      for(var i=0; i < this.articles.length; i++){
        if(this.articles[i].category == "sports"){
          result.push(this.articles[i])  
        }
      }
      return result
    },
    articles_technology: function () {
      var result = []
      for(var i=0; i < this.articles.length; i++){
        if(this.articles[i].category == "technology"){
          result.push(this.articles[i])  
        }
      }
      return result
    }
  },
  created : function (){
    this.showPage()
    this.fetchData()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

</style>



